import { Component, Prop, h, Host } from '@stencil/core';
import { classlist } from '../../../utils/classlist';

const variantsConfig = {
    variants: {
        layout: {
            horizontal: 'inline-block',
            vertical: 'flex',
        },
        active: {
            true: 'z-index-20 text-active-foreground',
            false: 'text-light',
        },
    },
    compoundVariants: [
        {
            layout: 'vertical',
            active: true,
            class: 'bg-active-foreground/10',
        },
    ],
};

export type Layout = 'vertical' | 'horizontal';

/**
 * @category Navigation
 * @description A tab component for the tab selector.
 */

@Component({
    tag: 'at-tab',
    shadow: false,
})
export class AtTab {
    /**
     * ID of the tab
     */
    @Prop()
    tab_id: string;

    /**
     * Title to be displayed in the tab
     */
    @Prop()
    tab_title: string;

    /**
     * Styling based on the layout of the tabs
     */
    @Prop()
    layout: Layout = 'horizontal';

    /**
     * Applies styling when active
     */
    @Prop()
    is_active: boolean;

    tabEl: HTMLElement;

    handleKeyDown(event: KeyboardEvent) {
        if (event.key === 'Enter' || event.key === ' ') {
            this.tabEl.click();
        }
    }

    render() {
        const getClassname = classlist(
            'group/tab text-button relative box-border flex cursor-pointer items-center justify-center gap-4 overflow-hidden rounded-md border border-solid border-transparent font-medium capitalize duration-150 ease-in-out outline-none focus:outline-none',
            variantsConfig,
        );

        const classname = getClassname({
            layout: this.layout,
            active: this.is_active,
        });

        return (
            <Host
                role="tab"
                aria-selected={this.is_active}
                data-active={this.is_active ? 'true' : 'false'}
                onKeyDown={(event) => this.handleKeyDown(event)}
                data-name={`tab-${this.tab_id}`}
            >
                <a
                    id={`tab-${this.tab_id}`}
                    tabindex="0"
                    class={classname}
                    ref={(el) => (this.tabEl = el)}
                >
                    {this.tab_title}
                    <slot />
                </a>
            </Host>
        );
    }
}
